@import "helpers/mixins";
@import "helpers/helpers";
@import "helpers/fonts";
@import "helpers/variables";

$easing: cubic-bezier(0.64, 0.04, 0.35, 1);

.timeline {
    padding: 70px 0 46px;
    background: $grey-green;
    overflow: hidden;
    @include desktop-less-lg {
        padding: 106px 0 120px; }
    @include desktop-less-md {
        padding: 113px 0 120px; }
    @include desktop-less-sm {
        padding: 86px 0 120px; }
    @include tablet-less-lg {
        padding: 75px 0 120px; }
    @include tablet-less-sm {
        padding: 72px 0 120px; }
    @include mob-less-lg {
        padding: 92px 0 120px; }
    &__content {
        h3 {
            line-height: 1.04;
            margin-bottom: 35px;
            @include desktop-less-lg {
                line-height: 1.1; }
            @include desktop-less-md {
                margin-bottom: 34px; }
            @include desktop-less-sm {
                line-height: 1.05;
                margin-bottom: 36px; }
            @include tablet-less-lg {
                font-size: 42px; }
            @include mob-less-lg {
                font-size: 30px;
                line-height: 1.07;
                margin-left: -2px; } }
        p {
            letter-spacing: 0px;
            font-size: 22px;
            line-height: 1.45;
            letter-spacing: -0.18px;
            margin-bottom: 89px;
            @include desktop-less-lg {
                margin-bottom: 87px; }
            @include hm-l;
            @include desktop-less-lg {
                line-height: 1.48;
                margin-bottom: 40px; }
            @include tablet-less-sm {
                font-size: 20px;
                line-height: 1.5; }
            @include mob-less-lg {
 } } }                //margin-bottom: 10px
    &__item {
        margin-bottom: 16px;
        @include desktop-less-lg {
            margin-bottom: 28px; }
        @include desktop-less-md {
            margin-bottom: 32px; }
        @include mob-less-lg {
            margin-bottom: 30px; }
        &:not(.is-active) {
            .state-icon {
                path {
                    &:nth-child(1), &:nth-child(2) {
                        transform: scaleX(0);
                        opacity: 0; } } } }
        &.is-active {
            h2 {
                color: $orange; }
            .state-icon {
                path {
                    &:nth-child(4), &:nth-child(5) {
                        transform: scaleX(0);
                        opacity: 0; } } }
            .timeline-box__in {
                opacity: 1;
                transform: translateY(0px);
                transition: opacity .2s .4s $easing, transform .8s $easing; } } }
    .state-icon {
        position: absolute;
        bottom: -15px;
        left: -42px;
        width: 22px;
        height: 32px;
        display: block;
        @include tablet-less-sm {
            left: - 100%/14*1; }
        path {
            transform-origin: 0 0;
            transition: transform .8s $easing, opacity .6s $easing; }
        &_lg {
            @include mob-less-lg {
                display: none; } }
        &_md {
            display: none; }
        &_sm {
            display: none;
            @include mob-less-lg {
                display: block;
                margin-left: -5px; } } } }

.timeline-box {
    &__in {
        opacity: 0;
        transform: translateY(-40px);
        transition: opacity .4s $easing, transform .6s $easing; }
    &__title {
        position: relative;
        z-index: 2;
        transform: translate3d(0,0,0);
        h2 {
            text-decoration: none;
            transition: color .6s $easing;
            position: relative;
            display: inline-block;
            cursor: pointer;
            font-size: 78px;
            line-height: 0.95;
            @include desktop-less-lg {
                font-size: 60px;
                font-size: 4.7vw;
                line-height: 0.93; }
            @include desktop-less-md {
                font-size: 52px;
                line-height: 0.92; }
            @include desktop-less-sm {
                font-size: 88px;
                line-height: 0.86; }
            @include tablet-less-lg {
                font-size: 68px;
                line-height: 0.94;
                font-size: 9.5vw; }
            @include tablet-less-sm {
                font-size: 68px; }
            @include mob-less-lg {
                font-size: 48px;
                font-size: 13vw;
                line-height: 0.92; }
            &:before {
                content: "";
                position: absolute;
                bottom: -2px;
                left: 0;
                width: 100%;
                height: 4px;
                border-radius: 4px;
                background: $orange;
                z-index: 1; }
            &:hover {
                color: $orange; } } }
    &__content {
        position: relative;
        background-color: $grey-bg;
        height: 0;
        visibility: hidden;
        &-in {
            padding: 40px 0 56px;
            @include desktop-less-lg {
                padding: 41px 0 28px; }
            @include mob-less-lg {
                padding: 41px 0 24px; } } }
    &__text {
        margin-bottom: 42px;
        h6 {
            line-height: 1.084;
            margin-bottom: 10px;
            width: 100%/10*6;
            @include desktop-less-lg {
                width: 100%/10*8; }
            @include desktop-less-md {
                width: 100%;
                line-height: 1.18;
                margin-bottom: 11px; }
            @include desktop-less-sm {
                font-size: 24px;
                line-height: 1.08;
                width: 100%/12*11; }
            @include tablet-less-lg {
                font-size: 22px;
                line-height: 1.18; }
            @include tablet-less-sm {
                width: 100%/14*12; }
            @include mob-less-lg {
                width: 100%/14*13;
                font-size: 20px;
                line-height: 1.2;
                margin-bottom: 9px; } }
        p {
            font-size: 16px;
            line-height: 1.5;
            letter-spacing: 0.01px;
            @include hm-l;
            width: 100%/10*5;
            @include desktop-less-lg {
                width: 100%/10*7;
                max-width: 440px; }
            @include desktop-less-md {
                width: 100%/10*8;
                letter-spacing: -0.01px; }
            @include desktop-less-sm {
                width: 100%/12*11;
                max-width: none;
                letter-spacing: 0.01px; }
            @include tablet-less-lg {
                max-width: 440px; }
            @include tablet-less-sm {
                width: 100%/14*12; }
            @include mob-less-lg {
                width: 100%/14*13; }
            br {
                @include mob-less-lg {
                    display: none; } } } }
    &__button {
        padding-top: 8px;
        padding-bottom: 28px;
        @include desktop-less-lg {
            padding-top: 0;
            padding-bottom: 41px; }
        @include mob-less-lg {
            padding-bottom: 47px; }
        .button {
            padding-left: 26px;
            padding-right: 26px; } } }



.timeline-quote {
    display: flex;
    //align-items: flex-end
    margin-bottom: 70px;
    @include desktop-less-md {
        margin-bottom: 46px; }
    @include desktop-less-sm {
        margin-bottom: 113px; }
    @include tablet-less-lg {
        margin-bottom: 24px; }
    @include tablet-less-sm {
        display: block; }
    @include tablet-less-sm {
        margin-bottom: 50px; }
    &__pic {
        width: 100%/10*3;
        @include desktop-less-sm {
            width: 100%/12*3; }
        @include tablet-less-sm {
            width: 100%;
            margin-bottom: 27px; }
        @include mob-less-lg {
            margin-bottom: 42px; }
        img {
            width: 100%;
            display: block;
            margin-bottom: 10px;
            @include desktop-less-lg {
                margin-bottom: 12px; } }
        figcaption {
            font-size: 12px;
            line-height: 1.17;
            @include desktop-less-lg {
                line-height: 1.33; } } }
    &__name {
        display: block;
        @include hm-m; }
    &__desc {
        @include hm-l; }
    &__text {
        width: 100%/10*5;
        font-size: 26px;
        line-height: 1.4;
        padding-top: 25.5%;
        @include hm-l;
        padding-left: 3px;
        position: relative;
        //top: 7px
        @include desktop-less-xl {
            padding-top: 24.5%;
 }            //top: 8px

        @include desktop-less-lg {
            width: 100%/10*7;
            max-width: 430px;

 }            //top: 4px
        @include r(1500) {
            padding-top: 23.5%; }
        @include desktop-less-md {
            width: 100%/10*6;
            font-size: 22px;
            line-height: 1.46;
            padding-top: 23%;
 }            //top: 0
        @include desktop-less-sm {
            width: 100%/12*8;
            font-size: 24px;
            line-height: 1.42;
            padding-top: 18%;
 }            //top: 1px
        @include tablet-less-lg {
            padding-top: 17.3%; }
        @include tablet-less-sm {
            width: 100%/14*13;
            max-width: none;
            padding-left: 0;
            padding-top: 0;
            font-size: 22px;
            line-height: 1.46; }
        @include mob-less-lg {
            // width: 100%
            font-size: 20px;
            line-height: 1.5; } }
    &__translation {
        font-size: 20px;
        margin-top: 30px;
        @include desktop-less-md {
            font-size: 18px; }
        @include tablet-less-sm {
            font-size: 16px; } } }
